export const Person = ({ name, age, sex, isMarried, partnerName }) => {
  const nameText = `My name is ${name}`;
  const ageText = `I am ${age}`;
  const marriagePartner = sex === 'm' ? 'wife' : 'husband';
  const marriageStatus = isMarried
    ? `${partnerName} is my ${marriagePartner}`
    : 'I am not married';

  return (
    <section className="Person">
      <h2 className="Person__name">{nameText}</h2>
      {age && <p className="Person__age">{ageText}</p>}
      <p className="Person__partner">{marriageStatus}</p>
    </section>
  );
};
